import React from "react";
import "../styles/About.css";
import FadeInSection from "./FadeInSection";

class About extends React.Component {
  constructor() {
    super();
    this.state = {
      expanded: true,
      activeKey: "1"
    };
    this.handleSelect = this.handleSelect.bind(this);
  }
  handleSelect(eventKey) {
    this.setState({
      activeKey: eventKey
    });
  }
  render() {
    const one = (
      <p>
        I am an innovative <b>Computer Science student at JUIT</b>, eager to build user-centric solutions. 
        My focus is on optimizing databases and contributing to impactful engineering teams.
      </p>
    );
    const two = (
      <p>
        I have a strong portfolio in <b>Deep Learning</b>, <b>Software Development</b>, and <b>UI/UX Design</b>.
        Whether it's building deepfake detection models or designing intuitive apps, I love solving complex problems.
      </p>
    );

    const tech_stack = [
      "C++ / C",
      "Python",
      "Java",
      "HTML / CSS",
      "SQL / MySQL",
      "PyTorch & OpenCV",
      "Machine Learning",
      "UI/UX Design"
    ];

    return (
      <div id="about">
        <FadeInSection>
          <div className="section-header">
            <span className="section-title">/ about me</span>
          </div>
          <div className="about-content">
            <div className="about-description">
              {one}
              {two}
              <p>Here are some technologies I have been working with recently:</p>
              <ul className="tech-stack">
                {tech_stack.map(function (tech_item, i) {
                  return (
                    <FadeInSection delay={`${i + 1}00ms`}>
                      <li>{tech_item}</li>
                    </FadeInSection>
                  );
                })}
              </ul>
            </div>
            <div className="about-image">
              <img src={"/me.jpg"} alt="Rajan Thakur" />
            </div>
          </div>
        </FadeInSection>
      </div>
    );
  }
}

export default About;